import React from 'react';
import './Video.css'

const Video = ({ video, selectedVideo }) => {
	const { title, thumbnails } = video.snippet;
	return (
		<div
			className='ui list'
			onClick={() => selectedVideo(video)}
		>
			<img
				className='ui image'
				src={thumbnails.high.url}
				alt={title}
				width='150px'
			/>
			<div>
				<h4>{title}</h4>
			</div>
		</div>
	);
};

export default Video;
