import React from 'react';
import SearchField from './component/SearchField';
import youtube from './api/youtube';
import VideoList from './component/VideoList';
import VideoDetail from './component/VideoDetail';

class App extends React.Component {
	state = { videoList: [], selectedVideo: null };

  componentDidMount() {
    this.submitValue('bollywood song')
  }

	submitValue = async (query) => {
		const res = await youtube.get('/search', {
			params: {
				q: query,
			},
		});
		this.setState({
			videoList: res.data.items,
			selectedVideo: res.data.items[0],
		});
	};

	onSelectedVideo = (video) => {
		this.setState({ selectedVideo: video });
	};

	render() {
		return (
			<div>
				<SearchField searchValue={this.submitValue} />
				<div
					className='ui grid container segment'
					style={{ marginTop: '40px' }}
				>
					<div className='ten wide column'>
						<VideoDetail video={this.state.selectedVideo} />
					</div>
					<div className='six wide column'>
						<VideoList
							listArray={this.state.videoList}
							selectedVideo={this.onSelectedVideo}
						/>
					</div>
				</div>
			</div>
		);
	}
}

export default App;
